<template>
  <div v-if="detail">
    <section class="playlist-header">
      <div class="pl-headerbg">
        <div
          class="mask"
          :style="{ backgroundImage: 'url(' + detail.coverImgUrl + ')' }"
        >
         <!-- <img :src="detail.coverImgUrl" alt=""> -->
        </div>
        <div class="content">
          <span class="return" @click="$router.go(-1)">
            <svg
              t="1630994806210"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="1901"
              width="200"
              height="200"
            >
              <path
                d="M532.526499 904.817574L139.506311 511.797385 532.526499 118.777197c12.258185-12.258185 12.432147-32.892131-0.187265-45.51052-12.707416-12.707416-32.995485-12.703323-45.511543-0.187265L75.166957 484.739123c-7.120165 7.120165-10.163477 17.065677-8.990768 26.624381-1.500167 9.755178 1.5104 20.010753 8.990768 27.491121l411.660734 411.660734c12.258185 12.258185 32.892131 12.432147 45.511543-0.187265 12.707416-12.707416 12.7023-32.995485 0.187265-45.51052z"
                p-id="1902"
                fill="#2c2c2c"
              ></path>
            </svg>
          </span>
          <span class="play-count">
            <img src="data:image/svg+xml;base64," />
            {{ detail.playCount | parseCount }}
          </span>
          <img class="u-img" :src="detail.coverImgUrl" alt="">
          <h2 class="uname">{{ detail.name }}</h2>
          <div class="user">
            <img class="user-img" :src="detail.creator.avatarUrl" />
            <h3 class="unick-name">{{ detail.creator.nickname }}</h3>
          </div>
        </div>
      </div>
      <h3 class="song-list">歌曲列表</h3>
    </section>

    <ul>
      <SongListItem
        v-for="(item, index) in detail.tracks"
        :key="item.id"
        :item="item"
        @change-current-song="
          $emit('change-current-song', item);
          $emit('change-current-play-list', detail.tracks);
        "
        :currentSongId="currentSongId"
        :playing="playing"
        :class="{ lt3: index < 3 }"
        >{{ index + 1 }}</SongListItem
      >
    </ul>
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";
export default {
  components: {
    SongListItem,
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
  filters: {
    parseCount: function(value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 100000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
  data: function () {
    return {
      detail: null,
      songsList: [],
    };
  },
  methods: {
    getPlayListDetail: function (id) {
      this.axios
        .get("http://apis.netstart.cn/music/playlist/detail", {
          params: {
            id,
          },
        })
        .then((res) => {
          // console.log(res);
          this.detail = res.data.playlist;
          console.log(res.data.playlist);
        });
    },
    getSongsList: function () {
      this.axios.get("/personalized?limit=6").then((res) => {
        console.log(res);
        this.songsList = res.result;
      });
    },
  },

  created: function () {
    // console.log( this.getPlayListDetail(this.$route.query.id));
    this.getPlayListDetail(this.$route.query.id);
  },
};
</script>

<style lang="less" scoped>
.playlist-header {
  .pl-headerbg {
    width: 100%;
    height: 180px;
    overflow: hidden;
    position: relative;
    .mask {
      width: 100%;
      height: 180px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      filter: blur(20px) brightness(0.8);
      transform: scale(1.5);
      z-index: -1;
    }
    .content{
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      .play-count{
        position: absolute;
        left: 0;
        display: inline-block;
        margin-left: 20px;
        width: 126px;
        background: rgba(196, 196, 196, 0.3);
        text-align: right;
        color: #fff;
        img{
          width: 12px;
        }

      }
      span.return {
        width: 30px;
        height: 30px;
        display: block;
        text-align: center;
        .icon {
          margin-top: 5px;
          width: 20px;
          height: 20px;
        }
      }
      .u-img{
        width: 126px;
        margin-left: 20px;
      }
      .uname{
        color: #fff;
        position: absolute;
        top: 30px;
        left: 155px;
        font-size: 18px;
      }
      .user{
        position: absolute;
        top: 90px;
        left: 155px;
        color: #fff;
        display: flex;
        align-items: center;
        .user-img{
          width: 30px;
          height: 30px;
          border-radius: 15px;
        }
        .unick-name{
          margin-left: 5px;
        }
      }

    }
  }
  .song-list{
    color: #666;
    background: #eeeff0;
    font-size: 12px;
    line-height: 2;
    padding-left: 5px;
  }
}
</style>
